export const REAL_VSO: VectorSpaceOperations<number> = {
  add: (u, v) => u + v,
  scale: (u, a) => u * a,
};

export const VEC2_VSO: VectorSpaceOperations<Vec2> = {
  add: addVec,
  scale: scaleVec,
};

export const COLOR32_VSO: VectorSpaceOperations<Color32> = {
  add: (u, v) => ({
    r: u.r + v.r,
    g: u.g + v.g,
    b: u.b + v.b,
    a: u.a + v.a,
  }),
  scale: (u, a) => ({
    r: u.r * a,
    g: u.g * a,
    b: u.b * a,
    a: u.a * a,
  }),
};

