function applyActiveFilterToTriplet(triplet: ColorTriplet): ColorTriplet {
  const s = 1.5;
  return {
    primary: saturate(s, triplet.primary),
    secondary: saturate(s, triplet.secondary),
    tertiary: saturate(s, triplet.tertiary),
  };
}

function applyInactiveFilterToTriplet(triplet: ColorTriplet): ColorTriplet {
  const s = 0.5;
  const a = 100;
  return {
    primary: { ...saturate(s, triplet.primary), a },
    secondary: { ...saturate(s, triplet.secondary), a },
    tertiary: { ...saturate(s, triplet.tertiary), a },
  };
}

function saturate(s: number, original: Color32): Color32 {
  const r = original.r / 255;
  const g = original.g / 255;
  const b = original.b / 255;

  const newR =
    r * (0.213 + 0.787 * s) + g * (0.715 - 0.715 * s) + b * (0.072 - 0.072 * s);
  const newG =
    r * (0.213 - 0.213 * s) + g * (0.715 + 0.285 * s) + b * (0.072 - 0.072 * s);
  const newB =
    r * (0.213 - 0.213 * s) + g * (0.715 - 0.715 * s) + b * (0.072 + 0.928 * s);

  return {
    r: toU8(255 * newR),
    g: toU8(255 * newG),
    b: toU8(255 * newB),
    a: original.a,
  };
}

function tripletWithAlpha(a: number, original: ColorTriplet): ColorTriplet {
  return {
    primary: { ...original.primary, a },
    secondary: { ...original.secondary, a },
    tertiary: { ...original.tertiary, a },
  };
}

function parseBlockDefString(
  blockDefString: string
): BlockDef<MainBlockEditorTypeParams> {
  const flags = {
    undeletable: blockDefString.includes("%%undeletable"),
    hermaphroditeCommand: blockDefString.includes("%%hermaphroditeCommand"),
    maleCommand: blockDefString.includes("%%maleCommand"),
    femaleCommand: blockDefString.includes("%%femaleCommand"),
    query: blockDefString.includes("%%query"),
    entryPoint: blockDefString.includes("%%entryPoint"),
  } as const;

  let remaining = blockDefString
    .replace(
      /%%(?:undeletable|hermaphroditeCommand|maleCommand|femaleCommand|query|entryPoint)/g,
      ""
    )
    .replace(/ {2,}/g, " ");
  const signature: CommandSignaturePart<MainBlockEditorTypeParams>[] = [];
  const dropdownDefaults: DropdownValueKind[] = [];
  const literalDefaults: string[] = [];

  while (remaining.length > 0) {
    const expressionMatch = remaining.match(/^\([^)]*\)/);
    if (expressionMatch !== null) {
      signature.push({
        kind: BlockSignaturePartKind.Expression,
      });

      literalDefaults.push(expressionMatch[0].slice(1, -1).trim());

      remaining = remaining.slice(expressionMatch[0].length);
      continue;
    }

    const dropdownMatch = remaining.match(/^\[[^\]]*\]/);
    if (dropdownMatch !== null) {
      const choices = dropdownMatch[0]
        .slice(1, -1)
        .split("||")
        .map((s) => s.trim());
      if (
        !choices.every(
          (s): s is DropdownValueKind =>
            typeof (DropdownValueKind as any)[s] === "string"
        )
      ) {
        throw new Error(
          "Illegal dropdown value in block editor string: " +
            JSON.stringify(blockDefString)
        );
      }

      signature.push({
        kind: BlockSignaturePartKind.Dropdown,
        choices,
      });

      dropdownDefaults.push(choices[0]);

      remaining = remaining.slice(dropdownMatch[0].length);
      continue;
    }

    const sequenceMatch = remaining.match(/^\{\}/);
    if (sequenceMatch !== null) {
      signature.push({
        kind: BlockSignaturePartKind.CommandSequence,
      });
      remaining = remaining.slice(sequenceMatch[0].length);
      continue;
    }

    const indexOfNextPart = remaining.search(/[([{]/);
    const labelText = remaining.slice(
      0,
      indexOfNextPart === -1 ? remaining.length : indexOfNextPart
    );
    const trimmedLabelText = labelText.trim();
    if (trimmedLabelText.length > 0) {
      signature.push({
        kind: BlockSignaturePartKind.Label,
        text: trimmedLabelText,
      });
    }

    remaining = remaining.slice(labelText.length);
  }

  const defaultArgs: BlockArgs<MainBlockEditorTypeParams> = {
    expressions: literalDefaults.map((source) => ({
      isLiteral: true,
      source,
    })),
    dropdowns: dropdownDefaults,
    sequences: signature
      .filter((part) => part.kind === BlockSignaturePartKind.CommandSequence)
      .map(() => []),
  };

  if (
    flags.query &&
    !flags.hermaphroditeCommand &&
    !flags.maleCommand &&
    !flags.femaleCommand &&
    !flags.entryPoint
  ) {
    return {
      kind: BlockDefKind.Query,
      isDeletable: !flags.undeletable,
      signature: signature.filter(
        (
          part
        ): part is
          | BlockSignatureLabel
          | BlockSignatureDropdown<MainBlockEditorTypeParams>
          | BlockSignatureExpression => {
          if (
            part.kind === BlockSignaturePartKind.Label ||
            part.kind === BlockSignaturePartKind.Dropdown ||
            part.kind === BlockSignaturePartKind.Expression
          ) {
            return true;
          }

          throw new Error(
            "Illegal block def string: " + JSON.stringify(blockDefString)
          );
        }
      ),
      defaultArgs,
    };
  }

  if (
    !flags.query &&
    [flags.hermaphroditeCommand, flags.maleCommand, flags.femaleCommand].filter(
      (x) => x
    ).length === 1
  ) {
    return {
      kind: BlockDefKind.Command,
      isDeletable: !flags.undeletable,
      isEntryPoint: flags.entryPoint,
      hasFemaleSocket: flags.femaleCommand || flags.hermaphroditeCommand,
      hasMaleSocket: flags.maleCommand || flags.hermaphroditeCommand,
      signature,
      defaultArgs,
    };
  }

  throw new Error(
    "Illegal block def string: " + JSON.stringify(blockDefString)
  );
}

export function mapBlockKindRecord<T, U>(
  src: ReadonlyRecord<BlockKind, T>,
  f: (val: T, kind: BlockKind) => U
): ReadonlyRecord<BlockKind, U> {
  return {
    [BlockKind.MoveForward]: f(
      src[BlockKind.MoveForward],
      BlockKind.MoveForward
    ),
    [BlockKind.Attack]: f(src[BlockKind.Attack], BlockKind.Attack),
    [BlockKind.WhenBattleStarts]: f(
      src[BlockKind.WhenBattleStarts],
      BlockKind.WhenBattleStarts
    ),
    [BlockKind.Heal]: f(src[BlockKind.Heal], BlockKind.Heal),
    [BlockKind.Forever]: f(src[BlockKind.Forever], BlockKind.Forever),
    [BlockKind.MoveBackward]: f(
      src[BlockKind.MoveBackward],
      BlockKind.MoveBackward
    ),
    [BlockKind.If]: f(src[BlockKind.If], BlockKind.If),
    [BlockKind.XInFront]: f(src[BlockKind.XInFront], BlockKind.XInFront),
    [BlockKind.And]: f(src[BlockKind.And], BlockKind.And),
    [BlockKind.Or]: f(src[BlockKind.Or], BlockKind.Or),
    [BlockKind.Not]: f(src[BlockKind.Not], BlockKind.Not),
    [BlockKind.Health]: f(src[BlockKind.Health], BlockKind.Health),
    [BlockKind.LessThan]: f(src[BlockKind.LessThan], BlockKind.LessThan),
    [BlockKind.Equal]: f(src[BlockKind.Equal], BlockKind.Equal),
    [BlockKind.GreaterThan]: f(
      src[BlockKind.GreaterThan],
      BlockKind.GreaterThan
    ),
  };
}

export function mapBlockKindRecordToInactiveBlockKindRecord<T, U>(
  src: ReadonlyRecord<BlockKind, T>,
  f: (val: T, kind: BlockKind) => U
): ReadonlyRecord<InactiveBlockKind, U> {
  return {
    [InactiveBlockKind.MoveForward]: f(
      src[BlockKind.MoveForward],
      BlockKind.MoveForward
    ),
    [InactiveBlockKind.Attack]: f(src[BlockKind.Attack], BlockKind.Attack),
    [InactiveBlockKind.WhenBattleStarts]: f(
      src[BlockKind.WhenBattleStarts],
      BlockKind.WhenBattleStarts
    ),
    [InactiveBlockKind.Heal]: f(src[BlockKind.Heal], BlockKind.Heal),
    [InactiveBlockKind.Forever]: f(src[BlockKind.Forever], BlockKind.Forever),
    [InactiveBlockKind.MoveBackward]: f(
      src[BlockKind.MoveBackward],
      BlockKind.MoveBackward
    ),
    [InactiveBlockKind.If]: f(src[BlockKind.If], BlockKind.If),
    [InactiveBlockKind.XInFront]: f(
      src[BlockKind.XInFront],
      BlockKind.XInFront
    ),
    [InactiveBlockKind.And]: f(src[BlockKind.And], BlockKind.And),
    [InactiveBlockKind.Or]: f(src[BlockKind.Or], BlockKind.Or),
    [InactiveBlockKind.Not]: f(src[BlockKind.Not], BlockKind.Not),
    [InactiveBlockKind.Health]: f(src[BlockKind.Health], BlockKind.Health),
    [InactiveBlockKind.LessThan]: f(
      src[BlockKind.LessThan],
      BlockKind.LessThan
    ),
    [InactiveBlockKind.Equal]: f(src[BlockKind.Equal], BlockKind.Equal),
    [InactiveBlockKind.GreaterThan]: f(
      src[BlockKind.GreaterThan],
      BlockKind.GreaterThan
    ),
  };
}
